import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import '../../css/Drawing/Global.css';

import {get, getUrlParams, isPlatformDesktop, loadFonts, openUrl} from "../../js/utils";
import {vk_local_users, getVKUsers} from "../../js/drawerapp/utils";
import {
    Panel, Snackbar,
    View
} from '@vkontakte/vkui';
import {Icon28ChevronRightOutline} from "@vkontakte/icons";

import fetch from 'node-fetch';

import {registerFont, createCanvas, loadImage} from 'canvas';
import request from "request";

import {ReactComponent as IconOnboard} from "../../assets/drawing/icons/Icon.svg";
import {ReactComponent as IconError} from "../../assets/drawing/icons/IconError.svg";

import Main from "./Main";
import Gallery from "./Gallery";
import Rating from "./Rating";
import Search from "./Search";
import Game from "./Game";
import Result from "./Result";
import Placeholder from "./Placeholder";

import Socket from '../../js/socket';
import eruda from 'eruda';
import Likes from "./Likes";
import PictureInfo from "./PictureInfo";

import (`../../css/Drawing/${window.location.search.length > 0 && JSON.parse('{"' + decodeURI(window.location.search.substring(1)).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') + '"}').vk_platform === 'desktop_web' ? 'Desktop' : 'Mobile'}.css`);

const
    socket = new Socket()
;
let
    bannersInterval,
    connectInterval
;

class Drawing extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activePanel: 'main',
            history: ['main'],

            colors: [
                '#000000',
                '#FFFFFF',
                '#E63902',
                '#EA5A03',
                '#FA8A00',
                '#FBB101',
                '#FDE401',
                '#85B201',
                '#019317',
                '#019C8B',
                '#035AA2',
                '#00247A',
                '#500A6D',
                '#A9236B'
            ],

            online: 0,

            drawerId: 0,
            lobbyId: 0,

            isOnlineDrawing: true,

            bannersCount: 3,
            banners: [],
            defaultBannerData: {
                title: 'Заголовок',
                domain: 'vk.com',
                trackingLink: 'https://vk.com',
                ctaText: 'Перейти',
                advertisingLabel: 'Реклама',
                iconLink: 'https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg',
                description: 'Описание рекламы',
                ageRestrictions: "14+",
                statistics: [
                    {url: '', type: 'playbackStarted'},
                    {url: '', type: 'click'}
                ]
            },
            connected: false,
            dataUpdated: false
        };

        this.componentDidMount = this.componentDidMount.bind(this);
        this.updateData = this.updateData.bind(this);
        this.back = () => {
            const {history, popout} = this.state;

            if (popout !== null && popout !== undefined)
                return;

            if (history.length > 1) {
                if (history[history.length - 1] === 'search') {
                    socket.call('games.search');
                    this.setState({
                        snackbar: <Snackbar onClose={() => this.setState({snackbar: null})}>
                            Поиск игры отменён
                        </Snackbar>
                    });
                }
                history.pop();
                this.setState({activePanel: history[history.length - 1], history});
                if (history[history.length - 1] !== 'search')
                    this.setState({snackbar: null});
            } else {
                bridge.send('VKWebAppClose', {status: 'success'});
            }
        };
        this.go = (panel) => {
            const {history} = this.state;
            if (history[history.length - 1] !== panel) {
                history.push(panel);
                window.history.pushState({activePanel: panel}, 'Title');
                this.setState({activePanel: panel, history, snackbar: null});
            }
        };
        this.setActivePanel = (panel) => {
            this.setState({activePanel: panel, history: [panel], snackbar: null});
        };
        this.setPopout = (popout) => {
            //window.history.pushState({popout}, 'Title');
            this.setState({popout});
        };
        this.socket = socket;

        window.onload = () => {
            const
                windowHeight = document.documentElement.clientHeight,
                scale = 100 / 812 * windowHeight / 100
            ;
            document.body.style.setProperty('--scale-value', scale);
        };
    }

    async componentDidMount() {
        loadFonts();

        window.addEventListener('popstate', e => {
            e.preventDefault();
            this.back();
        });

        bridge.subscribe(async ({detail: {type, data}}) => {
            if (type !== undefined) console.log(type, data);
            if (type === 'VKWebAppUpdateConfig') {
                const schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = data.scheme ? data.scheme === 'client_light' ? 'bright_light' : data.scheme : 'bright_light';
                document.body.attributes.setNamedItem(schemeAttribute);
            } else if (type === 'VKWebAppViewRestore') {
                this.setState({popout: null});
            }
        });

        const
            isShowOnboard = (await bridge.send('VKWebAppStorageGet', {keys: ['onboard']})).keys[0].value === '',
            isShowVkGroup = (await bridge.send('VKWebAppStorageGet', {keys: ['vk_group1']})).keys[0].value === '',
            vk_user = await bridge.send('VKWebAppGetUserInfo'),
            vk_user_ = getVKUsers([vk_user.id])
        ;
        await this.setState({isShowOnboard, isShowVkGroup, vk_user});

        bridge.send('VKWebAppEnableSwipeBack');

        this.connect();

        socket.subscribe('exit', r => {
            this.setState({placeholderText: r.title});
            this.setPopout(null);
            this.setActivePanel('placeholder');
            bridge.send('VKWebAppInit');
        });
        socket.onConnectionError((e) => {
            console.error(e);
            this.setState({placeholderText: 'Не удалось подключиться к серверу.'});
            this.setPopout(null);
            this.setActivePanel('placeholder');
            bridge.send('VKWebAppInit');
        });
        socket.onDisconnect(() => {
            this.setPopout(null);
            this.setActivePanel('placeholder');
            bridge.send('VKWebAppInit');
        });

        await new Promise(resolve =>
            socket.onConnect(async () => {
                console.log('Connected');
                this.setState({connected: true});
                this.setActivePanel('main');
                await this.updateData();

                if (isShowVkGroup && !isShowOnboard) {
                    setTimeout(() => {
                        this.go('group_vk');
                    }, 1000);
                }
                resolve(true);
            })
        );

        clearInterval(bannersInterval);
        bannersInterval =
            setInterval(() => {
                const {banners, bannersCount} = this.state;
                for (let i = 0; i < bannersCount; i++) {
                    bridge.send('VKWebAppGetAds', {}).then(data => {
                        if (!(banners[i] && banners[i] === false))
                            banners[i] = data;
                    });
                }
                this.setState({banners});
            }, 3000);

        bridge.send('VKWebAppInit');
    }

    connect() {
        socket.connect('https://draw.avocado.special.vk-apps.com', getUrlParams());
        setTimeout(() => {
            if (!this.state.dataUpdated) {
                this.updateData();
            }
            if (!this.state.connected) {
                clearInterval(connectInterval);
                connectInterval = setInterval(() => {
                    if (!this.state.connected) {
                        socket.connect('https://draw.avocado.special.vk-apps.com', getUrlParams());
                    } else {
                        clearInterval(connectInterval);
                    }
                }, 1000);
            }
        }, 1000);
    }

    async updateData() {
        await new Promise(resolve => {
            socket.call('users.getTopNumber', {}, r => {
                this.setState({top_number: r.response});
            });
            socket.call('users.getTop', {}, async r => {
                const
                    user_ids = r.response.map(value => value.id)
                ;
                await getVKUsers(user_ids);

                this.setState({top: r.response.map(value => ({...vk_local_users[value.id], ...value}))});
            });
            socket.call('users.get', {}, r => {
                this.setState({user: r.response});

                socket.call('games.getByDrawerId', {id: this.state.vk_user.id, limit: 10}, r => {
                    this.setState({gamesAsDrawer: r.response});
                    let user_ids = r.response.map(value => value.drawerId);

                    socket.call('games.getByWinnerId', {id: this.state.vk_user.id, limit: 10}, r => {
                        this.setState({gamesAsWinner: r.response});
                        user_ids = [...user_ids, ...r.response.map(value => value.drawerId)];

                        getVKUsers(user_ids);
                        this.setState({dataUpdated: true});
                        resolve(true);
                    });
                });
            })
        });

        this.setState({time: Date.now()});
        return true;
    }

    render() {
        const
            {
                activePanel,
                popout, snackbar,

                isShowOnboard, isShowVkGroup,
                vk_user, user,

                placeholderText,

                gamesAsWinner, gamesAsDrawer,

                drawerId, winnerId, lobbyId, gameId, word,
                like_picturesList, like_currentPictureSelected, like_method, like_param,

                isOnlineDrawing
            } = this.state,
            showConnecting = !(vk_user && user && gamesAsWinner && gamesAsDrawer)
        ;

        if (showConnecting) {
            //console.log(vk_user, user, gamesAsDrawer, gamesAsWinner);
        }
        return (
            <View
                activePanel={showConnecting ? 'connecting' : isShowOnboard ? 'onboard' : activePanel}
                popout={popout}
            >
                {
                    [
                        {
                            id: 'onboard',
                            component: <Placeholder
                                icon={<IconOnboard/>}
                                title='Добро пожаловать'
                                description='Здесь Вы можете почувствовать себя в роли как художника, так и Шерлока. Рисуйте картины и угадывайте чужие.'
                                buttonIcon={<Icon28ChevronRightOutline width={16} height={16}/>}
                                buttonText='Продолжить'
                                buttonOnClick={() => {
                                    bridge.send('VKWebAppStorageSet', {key: 'onboard', value: '1'});
                                    this.setState({isShowOnboard: false});
                                }}
                            />
                        },
                        {
                            id: 'connecting',
                            component: <Placeholder
                                icon={<IconOnboard/>}
                                title='Подключение'
                                description='Выполняется подключение к серверу...'
                            />
                        },
                        {
                            id: 'placeholder',
                            component: <Placeholder
                                icon={<IconError/>}
                                title='Соединение потеряно'
                                description={placeholderText}
                                buttonText='Переподключиться'
                                buttonOnClick={() => {
                                    this.connect();
                                }}
                            />
                        },
                        {
                            id: 'main',
                            component: <React.Fragment>
                                <Main t={this} time={this.state.time}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'gallery',
                            component: <React.Fragment>
                                <Gallery t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'rating',
                            component: <React.Fragment>
                                <Rating t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'likes',
                            component: <React.Fragment>
                                <Likes t={this}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'picture_info',
                            component: <React.Fragment>
                                <PictureInfo
                                    t={this}
                                    current={like_currentPictureSelected}
                                    method={like_method} param={like_param}
                                />
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'search',
                            component: <Search t={this}/>
                        },
                        {
                            id: 'game',
                            component: <React.Fragment>
                                <Game t={this} drawerId={drawerId} lobbyId={lobbyId} online={isOnlineDrawing}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'result',
                            component: <React.Fragment>
                                <Result t={this} drawerId={drawerId} winnerId={winnerId} word={word} gameId={gameId}/>
                                {snackbar}
                            </React.Fragment>
                        },
                        {
                            id: 'group_vk',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Group.png')} alt='icon'/>}
                                title='С возвращением!'
                                description='Не хочешь вступить в наше сообщество ВКонтакте? Именно там публикуются все крутые обновления'
                                buttonText='Вступить'
                                buttonOnClick={async () => {
                                    bridge.send('VKWebAppStorageSet', {key: 'vk_group1', value: '1'});
                                    this.setState({isShowVkGroup: false});
                                    try {
                                        await bridge.send('VKWebAppJoinGroup', {group_id: 208964042});
                                    } catch (e) {
                                    }
                                    this.back();
                                }}
                                buttonMode='gradient_gray'
                                buttonBack={true}
                                t={this}
                            />
                        },
                        {
                            id: 'vk_donut',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Donut.png')} alt='icon'/>}
                                title='VK Donut'
                                description='Чтобы получить доступ к дополнительным функциям, оформите подписку.'
                                buttonText='Перейти'
                                buttonOnClick={() => {
                                    openUrl('https://vk.com/donut/draw_app');
                                }}
                                buttonMode='gradient_donut'
                                buttonBack={true}
                                t={this}
                            />
                        },
                        {
                            id: 'vk_donut_ios',
                            component: <Placeholder
                                icon={<img src={require('../../assets/drawing/icons/Icon_Donut.png')} alt='icon'/>}
                                title='VK Donut'
                                description='Для уточнения информации, напишите нам в группу ВКонтакте.'
                                buttonText='Написать'
                                buttonOnClick={() => {
                                    openUrl('https://vk.com/draw_app');
                                }}
                                buttonMode='gradient_donut'
                                buttonBack={true}
                                t={this}
                            />
                        },
                    ].map(({id, component}, index) =>
                        <Panel id={id} key={`Panel__${index}`}>
                            {component}
                        </Panel>
                    )
                }
            </View>
        );
    }
}

export default Drawing;